<template>
  <div id="app">
    <section class="hero is-primary">
      <div class="hero-body">
        <div class="container has-text-centered">
          <h1 class="title">
            vue-fineuploader-dropzone
          </h1>
          <h2 class="subtitle">
            Demo
          </h2>
        </div>
      </div>
    </section>

    <section class="section is-medium">
      <div class="container">
        <div class="columns">
          <div class="column is-6">
          <fine-uploader :button="button"
                          :options="options"
                          :dropZone="dropzone"
                          @submit="onSubmit">
            <div class="drop-area is-fullwidth">
              <div class="drop-area-text">
                <p>
                  <span class="subtitle">Drop your images here</span>
                  <br>or<br>
                  <a href="#" class="button is-primary browse">Browse</a>
                </p>
              </div>
            </div>
          </fine-uploader>
        </div>
        <div class="column is-6">
          <pre>

            [
              // Submitted files will be added here...<br>
              <span v-for="item in queue">{
                id: {{ item.id }},
                name: {{ item.name }},
              },</span>
            ];

          </pre>
        </div>
        </div>
      </div>
    </section>

    <footer class="footer">
      <div class="container">
        <div class="content has-text-centered">
          <p>
            <strong>vue-fineuploader-dropzone</strong> by <a href="https://dieterstinglhamber.me">Elhebert</a>. The source code is licensed
            <a href="http://opensource.org/licenses/mit-license.php">MIT</a>.
          </p>
          <p>
            <a class="icon" href="https://github.com/Elhberet/vue-fineuploader-dropzone">
              <i class="fa fa-github"></i>
            </a>
          </p>
        </div>
      </div>
    </footer>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import FineUploader from './FineUploader';

export default {
  name: 'app',
  components: {
    FineUploader,
  },

  data() {
    return {
      button: '.browse',
      options: {
        autoUpload: false,
      },
      dropzone: {
        element: '.drop-area',
        dropActive: 'active',
      },
    };
  },

  computed: mapGetters(['queue']),

  methods: {
    ...mapActions([
      'addToQueue',
      'emptyQueue',
    ]),

    onSubmit(file) {
      this.addToQueue([file]);
    },
  },
};
</script>

<style lang="scss">
  @import '~bulma';
  $fa-font-path: '~font-awesome/fonts/';
  @import '~font-awesome/scss/font-awesome';

  .hero-body {
    .fa {
      vertical-align: baseline;
      color: red;
    }
  }

  .is-fullwidth {
    width: 100%;
  }

  .drop-area {
    display: flex;
    height: 150px;

    margin-bottom: 15px;

    border: 5px dashed whitesmoke;

    &.active { border-color: #00d1b2; }
  }

  .drop-area-text {
    display: flex;
    align-items: center;
    justify-content: center;

    text-align: center;

    width: 100%;
  }

  .delete {
    position: absolute;
    top: 2px;
    right: 2px;
    background-color: transparent;

    &:hover {
      background-color: transparent;
    }
  }

  .sortable-ghost {
    opacity: 0.5;
  }
</style>



