/* eslint-disable no-shadow, no-param-reassign, no-restricted-syntax, no-prototype-builtins */
const state = {
  images: [],
};

const mutations = {
  ADD_MEDIA: (state, images) => {
    state.images.push(...images);
  },
  UPDATE_PROGRESS_IMAGE: (state, image) => {
    const index = state.images.findIndex(i => i.id === image.id);
    state.images[index].progress = image.progress;
  },
  EMPTY_MEDIA_QUEUE: (state) => {
    state.images = [];
  },
};

const getters = {
  queue: state => state.images,
};

const actions = {
  addToQueue: ({ commit }, images) => {
    commit('ADD_MEDIA', images);
  },
  emptyQueue: ({ commit }) => {
    commit('EMPTY_MEDIA_QUEUE');
  },
};

export default {
  state,
  mutations,
  getters,
  actions,
};



